import React from 'react';
import styles from './login.module.css';
import { useForm } from 'react-hook-form';
import classNames from 'classnames';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../../redux/authReducer';
import { Navigate } from 'react-router-dom';
import { selectIsAuth } from '../../redux/authSelectors';
import { useState } from 'react';
import hideImg from '../../../src/assets/images/hidePass.png';
import showImg from '../../../src/assets/images/showPass.png';

export default function Login(props) {

  const [visible, setVisible] = useState(false);

  const dispatch = useDispatch();
  const isAuth = useSelector(selectIsAuth);

  const {
    clearErrors,
    setError,
    register,
    handleSubmit,
    formState: { errors, isValid }
  } = useForm({
    mode: 'onChange',
  });

  const onSubmit = (data => {
    dispatch(login(data.email, data.password, data.rememberMe, setError))
  })

  if (isAuth) {
    return <Navigate to='/profile' />
  }

  return (
    <div className={styles.loginPage}>
      <h1 className={styles.header}>Login</h1>

      <form onSubmit={handleSubmit(onSubmit)}
        onChange={() => {
          clearErrors('serverError');
        }
        }
      >
        <div className={styles.inputBox}>
          <input
            className={!errors.email
              ? styles.input
              : classNames(styles.input, styles.error)}
            type='text'
            name='email'
            placeholder='email'
            {...register('email',
              {
                required: 'This is require field',
                pattern: {
                  value: /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/,
                  message: "Please enter a valid email address"
                }
              })}
          />
          {errors.email && <div className={styles.errorMessage}>{errors.email.message}</div>}
        </div>

        <div className={styles.inputBox}>
          <input
            className={!errors.password
              ? styles.input
              : classNames(styles.input, styles.error)}
            type={visible ? 'text' : 'password'}
            name='password'
            placeholder='password'
            {...register('password',
              {
                required: 'This is require field',
                minLength: {
                  value: 4,
                  message: 'Please enter at least 4 characters'
                },
                maxLength: {
                  value: 15,
                  message: 'Please enter at most 15 characters'
                }
              })}
          />
          <button
            className={styles.passBtn}
            type='button'
            onClick={() => {
              visible ? setVisible(false) : setVisible(true)
            }}>
            <img src={visible ? hideImg : showImg} alt="icon" />
          </button>
          {errors.password && <div className={styles.errorMessage}>{errors.password.message}</div>}
        </div>

        <div className={classNames(styles.inputBox, styles.checkboxContainer)}>
          <input
            name='rememberMe'
            type='checkbox'
            {...register("rememberMe")}
            className={styles.checkbox}
          />
          <label className={styles.label} htmlFor='rememberMe'>Remember me</label>
        </div>

        <div>
          <input
            className={styles.btnSubmit}
            type='submit'
            value='Submit'
            disabled={!isValid}
          />
        </div>
      </form>
      {errors.serverError && <div className={styles.errorForm}>{errors.serverError.message}</div>}
    </div >
  );
}
